.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  margin: auto;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 1200;
}

.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: calc(100vw - 30px);
  max-height: calc(100vh - 24px);
  text-align: center;
}

.modalImage {
  display: block;
  width: 800px;
  margin: 0 auto;
}
